@import "/src/index.scss";

header {
  // height: 100vh;
  // padding-top: 7rem;
  overflow: hidden;
  margin: auto;
  // padding-left: 10%;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

.headerbtn {
  margin: 0 5px ;
}

.app-subtitle {
  margin: 0 0 3%
}

/* ================ CTA ================ */
.cta {
  // margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* ================ HEADER SOCIALS ================ */
.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: $color-primary;
}

/* ================ HEADER IMAGE ================ */
.me {
  background: linear-gradient($color-primary, transparent);
  // width: 50%;
  // width: 22rem;
  // height: 27.5rem;
  position: absolute;
  // left: calc(50% - 11rem);
  // left: 50%;
  // transform: translateX(-50%);
  margin: 3em;
  border-radius: 2em;
  overflow: hidden;
  display:flex;
  // padding: 3rem 0.5rem 0;
}

.about-me {
  width: 50%;
  // margin-left: 5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
}

/* ================ SCROLL DOWN ================ */
.scroll__down {
  position: absolute;
  right: -2.3rem;
  top: 30em;
  // transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

